import { Component } from "react";
import MovieMarcup from "../App/MovieMarcup/MovieMarcup";
import { NavLink, Route, Switch } from "react-router-dom";
import Cast from "../App/Cast/Cast";
import Reviews from "../App/Revievs/Reviews";
import styles from "../Styles/movie.module.css";
import services from "../Services/Services";

class Movie extends Component {
  state = {
    film: {},
    pageForReturn: {},
  };

  async componentDidMount() {
    const { location } = this.props;
    const { movieId } = this.props.match.params;

    const film = await services.getRequestedMovie(movieId);

    this.setState({
      film,
      pageForReturn: location?.state?.from || "/movies",
    });
  }

  goOnPreviousPage = () => {
    const { history } = this.props;

    history.push(this.state.pageForReturn);
  };

  render() {
    const { match } = this.props;
    const { title, vote_average, overview } = this.state.film;

    return (
      <>
        <button
          type="button"
          onClick={this.goOnPreviousPage}
          className={styles.button}
        >
          Go back
        </button>
        <MovieMarcup title={title} votes={vote_average} overview={overview} />
        <p>Additional info</p>
        <ul className={styles.list}>
          <li>
            <NavLink
              exact
              to={`${match.url}/cast`}
              className={styles.link}
              activeClassName={styles.activeLink}
            >
              Cast
            </NavLink>
          </li>
          <li>
            <NavLink
              exact
              to={`${match.url}/reviews`}
              className={styles.link}
              activeClassName={styles.activeLink}
            >
              Reviews
            </NavLink>
          </li>
        </ul>
        <Switch>
          <Route path={`${match.path}/cast`} component={Cast} />
          <Route path={`${match.path}/reviews`} component={Reviews} />
        </Switch>
      </>
    );
  }
}

export default Movie;
